html,
body {
  margin: 0;
  padding: 0;
}

body {
  font: 12px "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin: 0 auto;
  background: #f5f5f5;
  color: #4d4d4d;
  min-width: 230px;
  max-width: 550px;
  font-weight: 300;
}

.stopwatch {
  position: relative;
  border-radius: 2px;
}

.headline {
  width: 100%;
  font-size: 80px;
  font-weight: 50;
  text-align: center;
  color: rgba(13, 40, 197, 0.15);
  text-rendering: optimizeLegibility;
  margin: 20px auto;
}

.indicator {
  background: #fff;
  width: 100%;
  font-size: 100px;
  font-weight: 200;
  text-align: center;
  color: rgba(231, 40, 40, 0.15);
  text-rendering: optimizeLegibility;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 20%), 0 25px 50px 0 rgb(0 0 0 / 10%);
  margin: 0 auto;
}

.main {
  padding-top: 40px;
  padding-bottom: 20px;
  border: none;
  box-shadow: inset 0 -2px 1px rgb(0 0 0 / 3%);
  background: #fff;
}
.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0;
  padding: 0;
  position: relative;
  font-size: 24px;
}

.button {
  border-width: 1px;
  cursor: pointer;
  justify-content: center;
  padding-bottom: calc(0.5em - 1px);
  padding-left: 1em;
  padding-right: 1em;
  padding-top: calc(0.5em - 1px);
  text-align: center;
  white-space: nowrap;
  border-radius: 3px;
  margin: auto;
}

button:hover {
  background-color: rgb(250, 233, 78);
}

.is-dark {
  background-color: #4266ca;
  border-color: transparent;
  color: #fff;
}
